import React from 'react';
import ReactDOM from 'react-dom';

class App extends React.Component {
  render() {
    return (
      <div className="flex">
        <div className="flex down" style={{maxHeight: 953}}>
          <Controls
            name={this.props.module.name}
            onStart={this.props.onStart}
            player={this.props.player}/>
          <Oscilloscope
            width={1600}
            height={300}
            data={this.props.output}/>
          <Pattern
            pattern={this.props.module.patterns[this.props.module.patternTable[this.props.player.position]]}
            row={this.props.player.row}/>
        </div>
        <Samples player={this.props.player} samples={this.props.module.samples}/>
      </div>
    );
  }
}

class Pattern extends React.PureComponent {
  render() {
    return (
      <div className="flex">
        {this.props.pattern.channels.map(c => <Channel key={c} channel={c} row={this.props.row}/>)}
      </div>
    )
  }
}

class Channel extends React.PureComponent {
  render() {
    return (
      <div className="flex down">
        {this.props.channel.map((note, i) => <Note key={i} current={i === this.props.row} played={i < this.props.row} note={note}/>)}
      </div>
    )
  }
}

class Note extends React.PureComponent {
  render() {
    return (
      <p style={{
        backgroundColor: this.props.current ? '#eee' : '', 
        color: this.props.played ? '#333' : '#eee',
        margin: 0
      }}>
        <span className="sample">
          {this.props.note.sample}
        </span>

        <span className="note">
          {this.props.note.name} - {this.props.note.octave}
        </span>

        <span className="effect">
          &nbsp;{this.props.note.effect.raw.toString(16)}
        </span>
      </p>
    )
  }
}

class Controls extends React.Component {
  constructor(props) {
    super(props)
    this._started = false;
  }
  render() {
    return (
      <div className="flex" style={{maxHeight: 32}}>
        <h1>{this.props.name}: {this.props.player.position}</h1>
        <button onClick={this.back}></button>
        <button onClick={this.play}></button>
        <button onClick={this.forward}></button>
      </div>
    );
  }

  play = () => {
    console.log(this._started);
    if (!this._started) {
      console.log('wtf mate');
      this.props.onStart();
      this._started = true;
    } else {
      this.props.player.pause();
    }
  }

  back = () => {
    // TODO: reverse is a terrible name
    this.props.player.reverse();
  }

  forward = () => {
    this.props.player.forward();
  }
}

class Samples extends React.PureComponent {
  componentDidMount() {
    this._height = window.innerHeight;
  }

  render() {
    return (
      <div className="flex down" style={{overflowY: 'scroll', maxHeight: this._height}}>
        {this.props.samples.map((sample, i) => <Sample key={i} sample={sample}/>)}
      </div>
    );
  }
}

class Sample extends React.Component {
  render() {
    return (
      <div>
        <Oscilloscope data={this.props.sample.buffer} width={400} height={200} static/>
        <h3>{this.props.sample.name}</h3>
      </div>
    )
  }
}

class Oscilloscope extends React.Component {
  constructor(props) {
    super(props);
    this.paused = false;
    this.offscreen = document.createElement('canvas');
    this.offscreen.width = props.width;
    this.offscreen.height = props.height;
  }

  componentDidMount() {
    const node = ReactDOM.findDOMNode(this)
    const context = this.offscreen.getContext('2d');
    context.fillStyle = "#000";
    context.strokeStyle = "#f7e";
    this.paint(node, context, this.props.width, this.props.height);
  }

  componentDidUpdate() {
    if (this.props.static || this.paused) {
      return;
    }

    const node = ReactDOM.findDOMNode(this)
    const context = this.offscreen.getContext('2d');
    context.clearRect(0, 0, this.props.width, this.props.height);
    this.paint(node, context, this.props.width, this.props.height);
  }

  paint(node, context, width, height) {
    if (this.props.data === undefined) {
      return;
    }
    context.clearRect(0, 0, this.props.width, this.props.height);

    context.beginPath();
    const amplitude = (this.props.data[0] + 1) * height / 2;
    context.moveTo(0, amplitude);
    for (let i = 0; i < width; i++) {
      const offset = i; // this.props.data.length / width * i;
      const amplitude = (this.props.data[Math.floor(offset)] + 1) * height / 2;
      context.lineTo(i, amplitude, 1, 1);
    }
    context.stroke();
    this._lastUpdate = Date.now();

    const onscreenContext = node.getContext('2d');
    onscreenContext.clearRect(0, 0, this.props.width, this.props.height);
    onscreenContext.drawImage(this.offscreen, 0, 0);
  }

  togglePaused = () => {
    this.paused = !this.paused;
  }

  render() {
    return (
      <canvas onClick={this.togglePaused} width={this.props.width} height={this.props.height} style={{width: this.props.width/2 + 'px', height: this.props.height/2 + 'px'}}/>
    )
  }
}

export default App;



